import Error from '.Error';
import SyntaxError from '.SyntaxError';

//import * as options\$0 from './options\$0';

const NONE                = [];
let sourceLines                = NONE;
let lastLineIndex         = -1;
let lineIndex         = -1;

                                     
	                  
	                   
 
export const throws                                = (error             )        => {
	if ( sourceLines!==NONE ) {
		//done();
		//options\$0.clear();
		//error.stack = error.stack!.replace('\n', `\n    at (*.toml:${lineIndex+1}:0)\n@*.toml:${lineIndex+1}\n`);
		error.lineIndex = lineIndex;
		error.lineNumber = lineIndex+1;
	}
	throw error;
};

const previous = Symbol('previous');
             
	                          
	                 
  
const noop       = ()         => '';
noop[previous] = noop;

export let stacks_length = 0;
let last       = noop;

export const could = ()       => {
	if ( sourceLines!==NONE ) { throw Error('Internal error: parsing during parsing.'); }
};

const EOL = /\r?\n/;
export const todo = (source        )       => {
	sourceLines = source.split(EOL);
	lastLineIndex = sourceLines.length-1;
	lineIndex = -1;
	stacks_length = 0;
	last = noop;
};

export const next = ()         => sourceLines[++lineIndex] ;

export const rest = ()          => lineIndex!==lastLineIndex;

export const mark = ()         => lineIndex;

export const must = (message        , startIndex        )         => {
	lineIndex===lastLineIndex && throws(SyntaxError(message+' is not close until the end of the file, which started from line '+( startIndex+1 )+': '+sourceLines[startIndex]));
	return sourceLines[++lineIndex] ;
};

export const where = ()         => 'line '+( lineIndex+1 )+': '+sourceLines[lineIndex];

export const done = ()       => {
	sourceLines = NONE;
	last = noop;
};

export const stacks_pop = ()       => {
	const item       = last;
	last = last[previous] ;
	--stacks_length;
	return item;
};

export const stacks_push = (item      )       => {
	item[previous] = last;
	last = item;
	++stacks_length;
};

export const stacks_insertBeforeLast = (item      )       => {
	item[previous] = last[previous];
	last[previous] = item;
	++stacks_length;
};
